<template><section><html><head></head><body><h1>GroundPolylineGeometry</h1>
<p><code v-pre="">vc-geometry-polyline-ground</code> 组件用于加载贴地或 3DTiles 多段线。需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用，只能挂载到 <code v-pre="">vc-primitive-polyline-ground</code>。</p>
<h2>示例</h2>
<h3>加载贴地多段线几何体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-polyline-ground :appearance="appearance">
          <vc-instance-geometry>
            <vc-geometry-polyline-ground ref="polylineGround" :positions="positions" :width="2"></vc-geometry-polyline-ground>
          </vc-instance-geometry>
        </vc-primitive-polyline-ground>
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-layer-imagery>
          <vc-provider-imagery-arcgis-mapserver url="https://services.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer"></vc-provider-imagery-arcgis-mapserver>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-polyline-ground</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-polyline-ground</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"polylineGround"</span> <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions"</span> <span class="hljs-attr">:width</span>=<span class="hljs-string">"2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-polyline-ground</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-polyline-ground</span>&gt;</span>
