<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">appearance</span>: {},
        <span class="hljs-attr">geometryInstances</span>: {},
        <span class="hljs-attr">positions</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">100.1340164450331</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.05494287836128</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">108.08821010582645</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.05494287836128</span> }
        ]
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">polylineGround</span>.<span class="hljs-property">createPromise</span>.<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer, cesiumObject }</span>) =&gt;</span> {
        <span class="hljs-keyword">const</span> boundingSphere = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">BoundingSphere</span>.<span class="hljs-title function_">fromPoints</span>(cesiumObject.<span class="hljs-property">_positions</span>)
        viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">camera</span>.<span class="hljs-title function_">flyToBoundingSphere</span>(boundingSphere)
